<template>
  <b-row align-h="center">
    <b-col cols="12" md="10">
      <p
        class="mt-5"
      >RKS Gwiazda to warszawski wegaski klub sportowy dla wszystkich. Szczeglnie zapraszamy osoby kir, transpciowe, o nienormatywnej ekspresji lub tosamoci pciowej oraz przedstawicieli i przedstawicielki innych grup marginalizowanych. Jestecie u nas bezpieczne, poniewa tworzymy przestrze woln od seksizmu, lgbtq/fatfobii, rasizmu, nacjonalizmu, ageizmu i klasizmu. Chcemy trenowa w przyjaznej i bezpiecznej atmosferze bez uprzedze i mowy nienawici. eby adna osoba wiczca w klubie nie czua si upokorzona, wykluczona, ani dyskryminowana. Nie chcemy maczyzmu, agresji i napinki popularnych w innych klubach. Tak rozumiemy bezpieczestwo przestrzeni klubu.</p>
      <p
        class="mt-5"
      >RKS Gwiazda powsta aby zaspokoi potrzeb rozwijania kultury fizycznej w bezpieczniejszej atmosferze. Dla nas, dla was i wszystkich innych, ktre te tego chc!</p>
      <p
        class="mt-5"
      >Klub powsta te z naszej pasji do wolnoci i ruchu (Ruch - Wolno - Rwno). Jestemy osobami przekonanymi, e ruch, kondycja fizyczna, sprawno i wytrzymao oraz bycie w swoim ciele i dbanie o dobre z nim poczenie idzie w parze z wolnoci i rwnoci. Im lepiej bdziemy czu si we wasnych ciaach, tym efektywniej bdziemy w stanie wspiera wolno i rwno. Tym lepiej bdziemy przeywa nasze ycie.</p>
      <h4 class="mt-5">Zajcia</h4>
      <p
        class="mt-5"
      >Aktualnie w klubie prowadzimy nienormatywn sekcj sztuk/sportw walki Queer Fight Team: sporty walki w formule K-1 (Muay Thai, Kickboxing, Karate, Boks, Taekwon Do, Sanshou) w siedzibie Akademii Kung Fu "Tygrys i uraw" przy ul. Dugiej 44/50 w Warszawie.</p>
    </b-col>
  </b-row>
</template>


 <script>
export default {};
</script>


